
(function(){
	// Definio do mdulo
	var module = angular.module('Opcoes',[]);

	// Atricbuio de controllers ao mdulo
	module.controller('OpcoesController',OpcoesController);

	// Definio da funo controller
	function OpcoesController($scope,$cookies,$location){

		// Funo que direciona para a pgina de trocar senha
		$scope.onTrocarSenhaClick  = function(){
			$location.url("/senha");
		}

		// Funo que sai
		$scope.logout = function(){
			$scope.die();
		}
	}
})();